@import 'node_modules/bootstrap/scss/bootstrap.scss';
@import 'range.scss';

$baseColorN: #ed145b;
$baseColorL: #ff6e9e;
$baseColorV: #ffd3e2;
$baseColorH: #d60c4f;
$baseColorD: #af0940;

@mixin noselect {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

@mixin gradient($bg1, $bg2, $rotasi, $start, $end) {
	background: $bg1;
	background: linear-gradient($rotasi, $bg1 $start, $bg2 $end);
}

@mixin ButtonB($bgc, $bgch) {
	display: inline-block;
	padding: 20px 30px;
	font-weight: 500;
	letter-spacing: 1px;
	color: white;
	text-transform: uppercase;
	border-radius: 5px;
	background-color: $bgc;
	i {
		margin-left: 10px;
	}
	&:hover {
		background-color: $bgch;
		text-decoration: none;
		color: white;
	}
}

@keyframes shake {
	10%,
	90% {
		transform: translate3d(-1px, 0, 0);
	}

	20%,
	80% {
		transform: translate3d(2px, 0, 0);
	}

	30%,
	50%,
	70% {
		transform: translate3d(-4px, 0, 0);
	}

	40%,
	60% {
		transform: translate3d(4px, 0, 0);
	}
}

body:not(.user-is-tabbing) button:focus,
body:not(.user-is-tabbing) input:focus,
body:not(.user-is-tabbing) select:focus,
body:not(.user-is-tabbing) textarea:focus {
	outline: none;
}

.intro {
	@include gradient(#fff, #f6f6f6, 180deg, 0%, 50%);
	.logo-wrapper {
		text-align: left;
		padding-top: 90px;
		.logo {
			@include noselect;
			display: inline-block;
			img {
				height: auto;
				display: inline-block;
				margin-bottom: 15px;
			}
			p {
				font-weight: 300;
				font-size: 18px;
				color: $baseColorN;
				padding: 0;
				margin: 0;
			}
		}
		.lang {
			display: inline-block;
			span {
				display: inline-block;
			}
			ul {
				display: inline-block;
				margin: 0 0 0 10px;
				padding: 0;
				li {
					display: inline-block;
					list-style-type: none;
					margin-right: 10px;
					&:last-child {
						margin-right: 0;
					}
					a {
						color: #333;
						display: inline-block;
						padding: 1px 10px;
						background-color: #eee;
						border-radius: 5px;
					}
				}
			}
		}
	}
	article {
		padding-top: 60px;
		padding-bottom: 90px;
		text-align: left;
		h1 {
			margin-bottom: 60px;
			font-weight: 300;
			font-size: 42px;
			line-height: 1.5;
		}
		p {
			font-size: 18px;
			padding: 0;
			margin-bottom: 15px;
			span {
				@include noselect;
				padding: 3px 5px;
				color: white;
				font-size: 14px;
				border-radius: 5px;
				margin-right: 5px;
				font-weight: 500;
				letter-spacing: 1px;
				background-color: $baseColorN;
				i {
					margin-right: 3px;
				}
			}
		}
		a {
			@include noselect;
			@include ButtonB($baseColorN, $baseColorH);
		}
	}
}
h3.q-title {
	text-align: center;
	font-size: 38px;
	font-weight: 300;
	margin-bottom: 60px;
	small {
		display: block;
		margin-top: 15px;
		font-size: 18px;
	}
}
.solve {
	margin-top: 120px;
	.backdrop {
		display: none;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.75);
		z-index: 1;
	}
	h2 {
		text-align: center;
		font-size: 18px;
		font-weight: normal;
		position: relative;
		margin-bottom: 30px;
		padding-bottom: 15px;
		&::after {
			width: 80px;
			height: auto;
			border-top: 2px solid $baseColorN;
			content: '';
			position: absolute;
			bottom: 0;
			left: calc(50% - (80px / 2));
		}
	}
	.quiz {
		padding-left: 30px;
		padding-right: 30px;
		padding-top: 60px;
		padding-bottom: 60px;
		text-align: center;
		position: relative;
		z-index: 999;
		background-color: #fafafa;
		border-radius: 10px;
		.q-options {
			margin: 0;
			padding: 0;
			margin-bottom: 40px;
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			.q-option {
				padding: 30px 15px;
				width: 32%;
				display: inline-block;
				border: 1px solid #ddd;
				position: relative;
				border-radius: 5px;
				text-align: left;
				@include noselect;
				margin-bottom: 20px;
				&.qo-two {
					width: 49%;
				}
				i {
					font-size: 38px;
					color: $baseColorN;
					margin-right: 15px;
					display: block;
					float: left;
				}
				.q-opval {
					display: block;
					float: left;
					font-size: 18px;
					margin-top: 5px;
				}
				&:hover {
					cursor: pointer;
					box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
					.q-radio::after {
						background-color: $baseColorV;
					}
				}
				.q-radio {
					height: 40px;
					width: 40px;
					border: 1px solid $baseColorN;
					border-radius: 50%;
					position: absolute;
					right: 20px;
					top: calc(50% - 20px);
					z-index: -1;
					&::after {
						content: '';
						display: block;
						height: 20px;
						width: 20px;
						margin-top: 9px;
						margin-left: 9px;
						border-radius: 50%;
						background-color: transparent;
					}
				}
				&.option-active {
					border: 1px solid $baseColorN;
					.q-radio::after {
						background-color: $baseColorN;
					}
					&:hover {
						cursor: unset;
						box-shadow: none;
					}
				}
			}
		}
		.q-range {
			margin-bottom: 60px;
			.budget-price {
				margin-top: 30px;
				text-align: center;
				padding: 10px 60px;
				color: $baseColorN;
				font-size: 24px;
				font-weight: 500;
				letter-spacing: 1px;
				display: inline-block;
				border-radius: 5px;
				border: 2px solid $baseColorN;
			}
		}
		.qc-shake {
			animation: shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
			transform: translate3d(0, 0, 0);
			backface-visibility: hidden;
			perspective: 1000px;
		}
		.qc-target {
			textarea {
				text-align: center;
				padding: 10px 60px;
				color: #333;
				font-size: 18px;
				font-weight: 300;
				letter-spacing: 1px;
				display: block;
				width: 80%;
				border-radius: 5px;
				border: 2px solid $baseColorN;
				margin: 0 auto 60px auto;
				&:focus {
					border-color: $baseColorL;
				}
			}
		}
		.q-people {
			display: block;
			margin: 0 auto 60px auto;
			width: 50%;
			padding: 10px;
			color: #333;
			font-size: 18px;
			font-weight: 300;
			letter-spacing: 1px;
			display: block;
			border-radius: 5px;
			border: 2px solid $baseColorN;
		}
		.q-people-val {
			text-transform: capitalize;
		}
		.qc-contact {
			text-align: center;
			.q-details {
				display: inline-block;
				width: 50%;
				input {
					display: block;
					width: 100%;
					padding: 10px;
					color: #333;
					font-size: 18px;
					font-weight: 300;
					letter-spacing: 1px;
					display: block;
					border-radius: 5px;
					margin-bottom: 15px;
					border: 2px solid $baseColorN;
					&:last-child {
						margin-bottom: 60px;
					}
				}
			}
		}
		.qc-submited p {
			margin-bottom: 45px;
		}
	}
	.quiz-btn {
		@include ButtonB($baseColorN, $baseColorH);
	}
}

.behind {
	padding: 120px 0;
	text-align: center;
	p {
		font-weight: 300;
		font-size: 20px;
		letter-spacing: 1px;
		line-height: 3;
		text-align: left;
	}
	img {
		box-shadow: 0 0 30px rgba(100, 100, 100, 0.5);
	}
}

.img-title {
	display: inline-block;
	margin-bottom: 60px;
	h3.q-title {
		margin-top: 5px;
		margin-right: 30px;
		margin-bottom: 0;
		padding: 0;
		float: left;
	}
	img {
		box-shadow: none;
	}
}

.dontmiss {
	h3.q-title {
		font-weight: 400;
		margin-bottom: 15px;
	}
	text-align: center;
	padding: 60px 0;
	background-color: #eee;
	a {
		@include ButtonB($baseColorN, $baseColorH);
	}
}

.email {
	padding: 60px 0;
	h3.q-title {
		font-size: 30px;
		color: white;
		margin-bottom: 0;
		strong {
			font-weight: 600;
		}
	}
	background-color: $baseColorN;
}

.bottom {
	padding: 30px 0;
	color: white;
	background-color: #333;
	text-align: center;
	a {
		color: white;
	}
	.ft-links {
		margin: 0;
		display: inline-block;
		li {
			margin-right: 5px;
			float: left;
			list-style: none;
			color: #000;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
