input[type='range'] {
	height: 59px;
	-webkit-appearance: none;
	margin: 10px 0;
	width: 100%;
	&:hover {
		cursor: w-resize;
	}
}
input[type='range']:focus {
	outline: none;
}
input[type='range']::-webkit-slider-runnable-track {
	width: 100%;
	height: 10px;
	cursor: pointer;
	box-shadow: 0px 0px 0px #000000;
	background: #ed145b;
	border-radius: 1px;
	border: 0px solid #000000;
	&:hover {
		cursor: w-resize;
	}
}
input[type='range']::-webkit-slider-thumb {
	box-shadow: 0px 0px 0px #ffffff;
	border: 3px solid #ffffff;
	height: 50px;
	width: 50px;
	border-radius: 50px;
	background: #ed145b;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -21.5px;
	&:hover {
		cursor: w-resize;
	}
}
input[type='range']:focus::-webkit-slider-runnable-track {
	background: #ed145b;
}
input[type='range']::-moz-range-track {
	width: 100%;
	height: 10px;
	cursor: pointer;
	box-shadow: 0px 0px 0px #000000;
	background: #ed145b;
	border-radius: 1px;
	border: 0px solid #000000;
}
input[type='range']::-moz-range-thumb {
	box-shadow: 0px 0px 0px #ffffff;
	border: 3px solid #ffffff;
	height: 50px;
	width: 50px;
	border-radius: 50px;
	background: #ed145b;
	cursor: pointer;
	&:hover {
		cursor: w-resize;
	}
}
input[type='range']::-ms-track {
	width: 100%;
	height: 10px;
	cursor: pointer;
	background: transparent;
	border-color: transparent;
	color: transparent;
}
input[type='range']::-ms-fill-lower {
	background: #ed145b;
	border: 0px solid #000000;
	border-radius: 2px;
	box-shadow: 0px 0px 0px #000000;
}
input[type='range']::-ms-fill-upper {
	background: #ed145b;
	border: 0px solid #000000;
	border-radius: 2px;
	box-shadow: 0px 0px 0px #000000;
}
input[type='range']::-ms-thumb {
	margin-top: 1px;
	box-shadow: 0px 0px 0px #ffffff;
	border: 3px solid #ffffff;
	height: 50px;
	width: 50px;
	border-radius: 50px;
	background: #ed145b;
	cursor: pointer;
	&:hover {
		cursor: w-resize;
	}
}
input[type='range']:focus::-ms-fill-lower {
	background: #ed145b;
}
input[type='range']:focus::-ms-fill-upper {
	background: #ed145b;
}
